.loader {
    top: calc(50% - 10px);
    left: calc(50% - 42px);
    height: 20px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    display: block;
    -webkit-transform-origin: center;
            transform-origin: center;
    text-align: center;
    position: absolute;
    padding-left: 10px;
}

.loader span {
    display: inline-block;
    background: #0f7ae5;
    margin-right: 10px;
    box-sizing: content-box;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    -webkit-animation: wave 1s linear infinite;
            animation: wave 1s linear infinite
}

.loader span:nth-child(2) {
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
}

.loader span:nth-child(3) {
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;

}

.err {
    width: 100px;
    height: 100px;
    display: inline-block;
    box-sizing: content-box;
    position: absolute;
    border-radius: 50%;
    left: calc(50% - 50px);
    top: calc(50% - 60px);
}

.err::after,
.err::before {
    content: '';
    left: 0;
    top: calc(45%);
    background: rgb(185, 30, 30);
    display: inline-block;
    width: 100%;
    height: 10px;
    position: absolute;
    border-radius: 10px;
}

.err::after {
    -webkit-transform: rotateZ(45deg);
            transform: rotateZ(45deg);
}

.err::before {
    -webkit-transform: rotateZ(-45deg);
            transform: rotateZ(-45deg);
}

.err>span{
    font-family: inherit;
    font-weight: bold;
    color: black;
    width: 100%;
    height: 10px;
    position: absolute;
    bottom: -10px;
    text-align: center;

}

@-webkit-keyframes wave {

    0%,
    80%,
    100% {
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        opacity: 1;
    }

    40% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
        background: #0f7ae5;
        opacity: 0.5;
    }
}

@keyframes wave {

    0%,
    80%,
    100% {
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        opacity: 1;
    }

    40% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
        background: #0f7ae5;
        opacity: 0.5;
    }
}

