.accordion {
    text-align: center;
    position: relative;
    width: 80vw;
    border: none;
    display: inline-block;
    border-radius: 20px;
}

.accordion__item {
    width: 100%;
    /* border-radius: 20px; */
    margin-bottom: 5px;
}

.accordion__item:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-transition: 0.2 ease-in;
    transition: 0.2 ease-in;
}

/* .accordion__item + .accordion__item {
    border-bottom: 10px solid rgba(0, 0, 0, 0); 
    margin-bottom: 10px;
} */

.accordion__item--has-icon {
    position: relative;
}

.accordion__title {
    position: relative;
    background-color:  #009b9b !important;
    color: #FCFEFF;

    cursor: pointer;
    padding: 18px 0px 5px 0px;
    width: 100%;
    height: 10%;
    display: inline-block;
    text-align: center;

}

.accordion__title h2 {
    color: #FCFEFF;
}


.accordion__title:hover {
    background-color: #3b5c5e;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
}

.accordion__body {
    text-align:left;
    padding: 2vw;
    display: block;
    -webkit-animation: fadein 0.35s ease-in;
            animation: fadein 0.35s ease-in;
}

.accordion__body--hidden {
    display: none;
    opacity: 0;
    -webkit-animation: fadein 0.35s ease-in;
            animation: fadein 0.35s ease-in;
}

.accordion__title > *:last-child,
.accordion__body > *:last-child {
    margin-bottom: 0;
}

.accordion__arrow {
    display: inline-block;
    position: absolute;
    width: 24px;
    height: 12px;
    right: 5%;
    margin-top: -45px;
}

.accordion__arrow::after,
.accordion__arrow::before {
    display: block;
    position: absolute;
    top: 50%;
    width: 10px;
    height: 2px;
    background-color: currentColor;
    content: '';
}

.accordion__arrow::before {
    left: 4px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

[aria-expanded='true'] .accordion__arrow::before,
[aria-selected='true'] .accordion__arrow::before {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.accordion__arrow::after {
    right: 4px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

[aria-expanded='true'] .accordion__arrow::after,
[aria-selected='true'] .accordion__arrow::after {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.accordion__arrow::before,
.accordion__arrow::after {
    transition: transform 0.25s ease, -webkit-transform 0.25s ease;
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes move-down {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    10% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    20% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
    30% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes move-down {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    10% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    20% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
    30% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes move-up {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    10% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    20% {
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
    }
    30% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes move-up {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    10% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    20% {
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
    }
    30% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.accordion__title--animated:hover .accordion__arrow {
    -webkit-animation-name: move-down;
            animation-name: move-down;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.accordion__title--animated[aria-expanded='true']:hover .accordion__arrow {
    -webkit-animation-name: move-up;
            animation-name: move-up;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

